:root{
    --font:'Poppins', system-ui, -apple-system, BlinkMacSystemFont,
    'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
    'Helvetica Neue', sans-serif;

    --large-weight:900;
    --small-weight:100;
    --medium-weight:500;
}

*:where(:not(html, iframe, canvas, img, svg, video, audio, input, select):not(svg *, symbol *)) {
    all: unset;
    display: revert;
}


*,
*::before,
*::after {
    box-sizing: border-box;
}


a, button {
    cursor: revert;
}


ol, ul, nave {
    list-style: none;
}

img {
    max-inline-size: 100%;
    max-block-size: 100%;
}


table {
    border-collapse: collapse;
}


textarea {
    white-space: revert;
}


meter {
    -webkit-appearance: revert;
    appearance: revert;
}


:where(pre) {
    all: revert;
}


::placeholder {
    color: unset;
}


::marker {
    content: initial;
}


:where([hidden]) {
    display: none;
}


:where([contenteditable]:not([contenteditable="false"])) {
    -moz-user-modify: read-write;
    -webkit-user-modify: read-write;
    overflow-wrap: break-word;
    -webkit-line-break: after-white-space;
        line-break: after-white-space;
    -webkit-user-select: auto;
        user-select: auto;
}


:where([draggable="true"]) {
    -webkit-user-drag: element;
}


:where(dialog:modal) {
    all: revert;
}
html{
    font-family: var(--font);
    font-size: 62.5%;
}

main{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}

button{
    text-align: center;
    border: none;
    background: rgb(242, 147, 31);
    color: #fff;
    font-size: 1.9rem;
    font-weight: 500;
    height: 5.0rem;
    width: 28rem;
    justify-self: flex-start;
    cursor: pointer;
    border-radius: 0.5rem;
}

button:hover{
    background: rgb(215, 119, 23);
}

.form-password{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(33, 33, 33);
    width: auto;
    height: auto;
    border-radius: 1rem;
    padding: 3rem;
}

#range-words{
    width: 24rem;
    align-self: flex-start;
    margin: 3.4rem 0 2rem 0;
}

input[type="checkbox"]{
    cursor: pointer;
    margin: 1.5rem 0;
    width: 1.7rem;
}

.checkers{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    height: 12rem;
    width: 100%;
    margin: 2rem 0 4rem 0;
    cursor: pointer;
}

label{
    font-weight: 300;
    font-size: 1.7rem;
    color: #fff;
}

#output{
    width: 100%;
    border-radius: 0.3rem;
    text-align: center;
    font-weight: 300;
    padding: 0.5rem;
    font-size: 2.5rem;
    color: #fff;
    border: solid;
    border-color: rgb(242, 147, 31);;
}

.value-range{
    font-size: 2rem;
    margin-left: 1.3rem;
}


